<template>
    <div id='play'>
        <headerBar></headerBar>
        <div>
            <div class="leftBar">
                <h3>正在播放：{{ info.name }} <small>——第{{ playing }} 集</small></h3>
                <video controls crossorigin playsinline :src="'./video/violet.mp4'" class="video"></video>
                <div class="footer">
                    <div></div>
                </div>
            </div>
            <div class="rightBar">
                <div class="title">
                    <div class="moveBar" :style="`margin-left:${move}rem;`"></div>
                    <span @click="move = 1">简介</span>
                    <!-- <span @click="move = 4">评论</span> -->
                </div>
                <h4>{{ info.name }}</h4>
                <div class="introduce">
                    <input type="checkbox" id="more">
                    <p>
                        {{ info.introduce }}
                    </p>
                    <label for="more"></label>
                </div>
                <div class="adList">
                    <ul>
                        <li>
                            <a href="https://store.steampowered.com/app/1238840/Battlefield_1/" target="_blank">
                                <img :src="'./img/ad_bf1.png'" alt="1">
                            </a>
                        </li>
                        <li>
                            <h2>广告位招租</h2>
                        </li>
                        <li>
                            <h2>
                                广告位招租
                            </h2>
                        </li>
                    </ul>
                </div>
                <div class="site">
                    <dl>
                        <dt>
                            <p>选集( {{ playing }} / {{ info.site }})</p>
                            <p>keyAnime动漫主线</p>
                        </dt>
                        <dd v-for="(i, index) in parseInt(info.site)" :key="i" v-if="info.site - 1"
                            :class="{ host: index + 1 == playing }" @click="go(index)">
                            第{{ i }}集
                        </dd>
                        <dd v-if="!(info.site - 1)" class="host">
                            剧场版
                        </dd>
                    </dl>
                </div>

            </div>
        </div>
    </div>
</template>
<script>
import plyr from 'plyr';
import searchBar from './rightBar/searchBar.vue';
import Message from '@yaohaixiao/message.js/message'
import headerBar from '../content/headerBar.vue'
export default {
    name: 'play',
    data() {
        return {
            move: 1,
            playing: this.$route.params.playing
        }
    },
    computed: {
        info() {
            let { index, target } = this.$route.params
            return this.$store.state[target][index]
        }
    },
    components: {
        searchBar,
        headerBar
    },

    methods: {
        go(index) {
            if (index + 1 == this.playing) {
                return
            }
            this.$store.commit('goPlay', { router: this.$router, info: this.info, playing: index + 1 })
        }
    },
    mounted() {
        new plyr(".video")
        Message.info({
            duration: 2,
            message:'为节省空间，只展示一个视频。'
        })
    }
}
</script>
<style scoped lang="less">
@import '../../../node_modules/plyr/dist/plyr.css';

.host {
    color: var(--bgWihte);
    background-color: var(--hovercolor);
}

#play {

    //视频播放主体
    &>div {
        height: calc(100svh - 4.1rem);
        border: .01rem var(--shadowColor);
        border-style: groove none none outset;
        border-radius: .8rem 0 0 0;
        padding: 0 1%;
        display: flex;
        box-sizing: border-box;
    }

    .leftBar {
        height: 98%;
        margin-top: 1%;
        border-radius: .8rem .8rem 0 0;
        width: 70%;
        // overflow: hidden;
        background-color: var(--bgWihte);
        display: flex;
        flex-direction: column;

        h3 {
            flex-grow: 1.2;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        .footer {
            flex-grow: 0.8;
            overflow: hidden;

            div {
                width: 200%;
                height: 100%;
                background-image: url('../../../public/img/back15_047.png');
                animation: abc 30s infinite ease;
            }

            @keyframes abc {
                0% {
                    transform: translate(0);
                }

                100% {
                    transform: translate(-50%);
                }
            }
        }

        video {
            width: 100%;
        }
    }


}

.rightBar {
    width: 29%;
    margin-left: 1%;
    height: 98%;
    margin-top: 1%;
    border-radius: .8rem .8rem 0 0;
    background-color: var(--bgWihte);
    padding: 1% 2% 0;
    box-sizing: border-box;
    overflow: hidden;
    display: flex;
    flex-direction: column;

    &>*:not(.site, .title) {
        margin: 1rem 0;
    }

    .title {

        // border-bottom: 1px solid var(--shadowColor);
        span {
            margin-left: 1rem;
        }

        position: relative;

        .moveBar {
            position: absolute;
            bottom: 0;
            width: 2rem;
            height: .25rem;
            background-color: var(--hovercolor);
            margin-bottom: -.25rem;
            transition: margin-left .25s;
        }
    }

    .introduce {

        label {
            display: inline-block;
            margin-left: calc(100% - 2rem);

            &::before {
                content: '展开';
            }
        }

        p {
            overflow: hidden;
            display: -webkit-box;
            -webkit-line-clamp: 3;
            -webkit-box-orient: vertical;
        }

        #more {
            display: none;

            &:checked~p {
                overflow: visible;
                display: block;
            }

            &:checked~label::before {
                content: '收起';
            }
        }
    }

    .adList {
        li {
            cursor: pointer;
            width: 100%;
            height: 8vh;
            margin-top: .5rem;
            background-color: var(--bglight);
            display: flex;
            justify-content: center;
            align-items: center;
        }

        img,
        a {
            width: 100%;
            height: 100%;
            object-fit: fill;
        }
    }

    .site {
        margin-top: auto;

        dl {
            display: flex;
            flex-direction: column;
            height: 32vh;
            overflow-y: auto;
            background-color: var(--bglight);
            border-radius: .5rem .5rem 0 0;
            gap: .2rem;

            dt {
                padding: .5rem .5rem 0;
                box-sizing: border-box;

                p:nth-of-type(2) {
                    margin: 1rem 0;
                }
            }

            dd {
                cursor: pointer;
                display: flex;

                align-items: center;
                width: 100%;
                height: 2rem;
                padding: .5rem 1rem;
                font-size: .8rem;
                box-sizing: border-box;

                &:hover {
                    color: var(--bgWihte);
                    background-color: var(--hovercolor);
                }
            }


        }
    }
}
</style>