import Vuex from "vuex";
import Vue from "vue";
import tvAnimeList from '../../public/assets/tvAnimeList.json';
import filmAnimeList from '../../public/assets/filmAnime.json';
console.log(tvAnimeList)
Vue.use(Vuex);
const store = new Vuex.Store({
    state: {
        tvAnimeList: tvAnimeList.data,
        filmAnimeList: filmAnimeList.data,
        logoinUesr: JSON.parse(localStorage.getItem('logoinUesr')) || '',
        hideAsid: false
    },
    mutations: {
        goPlay(state, { router, info, playing = 1 }) {
            let { index, target } = state.tvAnimeList.indexOf(info) == -1 ? { index: state.filmAnimeList.indexOf(info), target: 'filmAnimeList', playing: 1 } :
                { index: state.tvAnimeList.indexOf(info), target: 'tvAnimeList', playing: 1 };

            router.push({ path: `/play/${target}/${index}/${playing}` })
           

        },
        setLogoinUesr(state, uesrInfo) {
            state.logoinUesr = uesrInfo
        },
    },

})
console.log(store)
export default store;