import Vue from "vue";
import VueRouter from "vue-router";
import rightBar from '../components/page/rightBar/rightBar.vue'
import content from '../components/content/content'
import pokemon from '../components/content/pokemon/pokemon'
import play from '../components/page/play'
import logoin from '../components/page/logoin/logoin.vue'
import search from '../components/page/search.vue'
import collect from '../components/page/collect.vue'
import Message from "@yaohaixiao/message.js";
Vue.use(VueRouter);
const router = new VueRouter({
    routes: [
        {
            name: 'rightBar',
            path: '/index',
            component: rightBar,
            redirect: '/index/recommend',
            meta: {
                msg: 'keyAnime动漫首页'
            },
            children: [
                {
                    name: 'recommend',
                    path: 'recommend',
                    component: content,
                    meta: {
                        msg: '推荐',

                    }
                },
                {
                    name: 'TVAnime',
                    path: 'TVAnime',
                    component: content,
                    meta: {
                        msg: 'TV动画',

                    }
                },
                {
                    name: 'filmMode',
                    path: 'filmMode',
                    component: content,
                    meta: {
                        msg: '剧场版',

                    }
                },
                {
                    name: '4kMode',
                    path: '4kMode',
                    component: content,
                    meta: {
                        msg: '4k专区',

                    }
                }
            ]
        },
        {
            path: '/play/:target/:index/:playing',
            name: "play",
            component: play,
            meta: {
                msg: '正在播放'
            },
        },
        {
            path: '/logoin',
            name: "logoin",
            component: logoin,
            meta: {
                msg: '登录幻想乡'
            }
        },
        {
            path: '/search',
            name: "search",
            component: search,
            meta: {
                msg: '登录幻想乡'
            }
        },
        {
            path: '/collect',
            name: "collect",
            component: collect,
            meta: {
                msg: '登录幻想乡'
            }
        },
        {
            path: '/pokemon',
            name: "pokemon",
            component: pokemon,
            meta: {
                msg: '登录幻想乡'
            }
        },
    ]
})
router.beforeEach((to, from, next) => {
    document.title = to.meta.msg
    if (from.fullPath == '/') {
        if (to.fullPath == '/') {
            router.push({ path: "/index" })
            console.log(1)
            return 0
        }
    }
    
    
    // 使用 router.match 方法匹配给定的路径和路由配置
    const matchedRoute = router.match(to.path);
    if (to.fullPath == '/' || !matchedRoute.name) {
        Message.error({
            duration: 5,
            message: '错误代码：404!为您返回上一级'
        })
        router.go(-1)
        return
    }
    next()

})
export default router;