<template>
    <header>
        <p>Key动漫！</p>
        <nav>
            <ul>
                <li @click="movebar(1)"><router-link to="/index/recommend" active-class=" host">推荐</router-link></li>
                <li @click="movebar(2)"><router-link to="/index/TVAnime" active-class=" host">TV动画</router-link></li>
                <li @click="movebar(3)"><router-link to="/index/filmMode" active-class=" host">剧场版</router-link></li>
                <li @click="movebar(4)"><router-link to="/index/4kMode" active-class=" host">4k专区</router-link></li>

            </ul>
            <p :style="`left:${movebarleft}`"></p>
        </nav>
           <searchBar></searchBar>
            <!-- <ul>
                <li style="--i:0"></li>
                <li style="--i:1"></li>
                <li style="--i:2"></li>
            </ul> -->
    </header>
</template>
<script>
import searchBar from './searchBar.vue'
export default {
    name: 'headerMenu',
    data() {
        return {
            movebarleft: "10%"
        }
    },
    components:{
        searchBar
    },
    methods: {
        // 移动条的位置 及 路由选项点击换色
        movebar(index) {
            this.movebarleft = (25 * (index - 0.5) - 2.5) + "%";
        }
    },
    mounted() {
        this.$router.options.routes[0].children.forEach((e, index) => {
            e.name == this.$route.name && this.movebar(index + 1);
        })
    }
}
</script>
<style scoped>
@font-face {
  font-family: "logoFont";
  src: url('./font.ttf');
}
header>p{
    font-family: "logoFont";
    font-size: 2rem;
}
.host {
    color: var(--hovercolor);
    font-weight: bold;

}

header {
    height: 4rem;
    padding: 0 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

header>*>ul {
    display: flex;
}

/* 专栏底部移动条 */
header>nav {
    width: 30%;
    position: relative;
}

header>nav ul {
    width: 100%;
    text-align: center;
}

header>nav p {
    position: absolute;
    bottom: 0;
    width: 5%;
    height: .25rem;
    background-color: var(--hovercolor);
    margin-bottom: -.75rem;
    transition: left .25s;
}

/* 四个专栏 */
header>nav ul li {
    width: 25%;
    user-select: none;
    cursor: pointer;
    text-wrap: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

header>nav ul li:hover a {
    color: var(--hovercolor);
}

/* 右侧霓虹灯 */
header>div ul li {
    width: .75rem;
    height: .75rem;
    background-color: red;
    filter: hue-rotate(calc(var(--i) * 120deg));
    margin: 0 .5rem;
    border-radius: 50%;
    animation: nihong 2s linear infinite alternate;
}

@keyframes nihong {
    from {
        background-color: red;
    }

    to {
        background-color: green;
    }
}
</style>