<template>
    <main>
        <stage />
        <indexAside :style="`${asideW}`" @colseAside="colseAside"></indexAside>
        <!-- 打开侧边栏 -->
        <div class="asideBar" @click="asideW = 'padding: 0 1rem;'; showAsideBar = false" v-if="showAsideBar">||</div>
        <headerMenu v-show="changeHeaderBar"></headerMenu>
        <RouterView :key="$route.fullPath"></RouterView>
    </main>
</template>
<script>
import headerMenu from './page/rightBar/headerMenu.vue';
import indexAside from './content/indexAside.vue';
import stage from './content/pokemon/stage.vue';
export default {
    name: 'asideMenu',
    data() {
        return {
            asideW: 'padding: 0 1rem;',
            showAsideBar:false,
        }
    },computed:{
        changeHeaderBar(){
            return this.$route.fullPath.includes('index') 
        }
    },
    components:{
        headerMenu,
        indexAside,
        stage
    },
    methods:{
        colseAside(){
            this.asideW = 'padding: 0;width:0px;'; this.showAsideBar = true
        }
    }
}
</script>
<style scoped>
.host {
    color: var(--hovercolor);
    font-weight: bold;

}

main {
    display: grid;
    grid-template: 4rem 1fr / auto 1fr;
    min-height: 100vh;
    background-color: var(--bglight);
    /* min-width: 1280px; */
    position: relative;
}


.asideBar {
    position: absolute;
    top: 50%;
    left: 0;
    display: flex;
    align-items: center;
    border-radius: .3rem;
    padding: .2rem;
    background-color: var(--bgWihte);
    z-index: 5;
    transform: translate(0, -50%);
}
</style>
