<template>
    <div class='pokemon_' tabindex="0">
        <div></div>
        <div class="bg">
            <img src="./精灵球.png" class="qiu">
            <img src="./bg.png">
            <div class="player">
                <img src="./水跃鱼.png">
            </div>
            <div class="playerInfo">
                <div>
                    <p>水跃鱼<b>♀</b></p>
                    <p>Lv:5</p>
                </div>
                <div>
                    <p>HP</p>
                    <div class="HPvalue">
                        <div :style="`width: calc(${P_HPW} - .5rem);`" style="transition: width 1s;"></div>
                    </div>
                </div>
                <div>
                    {{ P_HPvalue }} / {{ P_HP }}
                </div>
            </div>
            <div class="NPC">
                <img src="./鲤鱼王.png" alt="">
            </div>
            <div class="NPCInfo">
                <div>
                    <p>鲤鱼王<b>♀</b></p>
                    <p>Lv:2</p>
                </div>
                <div>
                    <p>HP</p>
                    <div class="HPvalue">
                        <div :style="`width: calc(${N_HPW} - .5rem);`" style="transition: width 1s;"></div>
                    </div>
                </div>
            </div>
        </div>

        <div class="title">
            <div :class="{ msg: true, msgFull: msging, hidden: selecting }">
                <!-- 消息 -->
                <p v-html="msg"> </p>
                <span v-show="msging">
                    <img src="./ok_hover.png" class="back">
                    <img src="./cancel_hover.png" class="back">
                </span>

            </div>
            <div :class="{ selectBar: true, selectBarFull: selecting, hidden: msging }">
                <div class="selectBar1">
                    <div>
                        <div class="pointer"></div>
                        <div class="select" v-for="(s, index) in 4" :key="s">
                            {{ selects[index] && selects[index].hasOwnProperty("name") ? selects[index].name : '-' }}
                        </div>

                    </div>
                </div>
                <div :class="{ selectBar2: true, hidden: !selecting }">

                    <div>
                        <p v-html="msg" style="width: 10rem;font-size: 2rem;"></p>
                        <p>
                            <img src="./ok_hover.png" class="ok"
                                style="width: var(--p_fontsize);height: var(--p_fontsize);">
                            <img src="./cancel_hover.png" class="back"
                                style="width: var(--p_fontsize);height: var(--p_fontsize);">
                        </p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import gsap from 'gsap'
export default {
    name: 'pokemon_',
    data() {
        return {
            msging: false,
            selecting: false,
            selects: [
                { name: '战斗' },
                { name: '背包' },
                { name: '宝可梦' },
                { name: '逃跑' }
            ],
            msg: '想要<br/>水跃鱼做什么？',
            N_HP: 20,
            P_HP: 20,
            N_HPvalue: 20,
            P_HPvalue: 20,
        }
    },
    computed: {
        N_HPW() {
            return (100 / this.N_HP) * this.N_HPvalue + '%'
        },
        P_HPW() {
            return (100 / this.P_HP) * this.P_HPvalue + '%'
        }
    },
    mounted() {



        const shuiyueyu = {
            name: '水跃鱼',
            HP: 20,
            HPvalue: 20,
            defenses: 0,
            msg() { return '想要<br/>' + this.name + '做什么？' },
            selects: [
                {
                    name: '技能',
                    selecting: true,
                    msging: false,
                    container: [
                        {
                            name: '冲击',
                            describe: '用整个身体撞向对手进行攻击。',
                            effects(that) {
                                that.NPC.HPvalue += (that.NPC.defenses - 5)
                                that.animations[0].container()
                                that.vue.$data.msg = '造成了' + -(that.NPC.defenses - 5) + '点伤害'
                                that.vue.$data.N_HPvalue = that.NPC.HPvalue
                                if (that.NPC.HPvalue <= 0) {
                                    that.gameOver(that)
                                    that.vue.$data.msg = that.NPC.name + '倒下了'
                                    that.animations[2].container()
                                    that.animations[1].container()
                                }

                            }
                        },
                        {
                            name: '叫声',
                            describe: '引开注意力使其疏忽，从而降低对手的防御。',
                            effects(that) {

                                that.NPC.defenses += -1
                                that.vue.$data.msg = '降低了' + that.player.name + '的防御力'
                            }
                        }
                    ]
                },
                {
                    selecting: true,
                    msging: false,
                    name: '背包',
                    container: [
                        {
                            name: '伤药',
                            describe: '回复10ＨＰ',
                            effects(that) {
                                that.vue.$data.msg = '恢复了' + that.player.name + '10点HP'
                                that.player.HPvalue += 10;
                                that.player.HPvalue > 20 && (that.player.HPvalue = 20)
                                that.vue.$data.P_HPvalue = that.player.HPvalue
                            }
                        },
                        {
                            name: '精灵球',
                            describe: '有概率捕捉野生宝可梦',
                            effects(that) {
                                that.animations[3].container()
                                if (Math.random() > 0.9) {
                                    that.vue.$data.msg = '...'
                                    setTimeout(() => {
                                        that.vue.$data.msg = '捕捉到了' + that.NPC.name + '!'
                                        that.gameOver(that)
                                    }, 4000)


                                } else {
                                    that.vue.$data.msg = '...'
                                    setTimeout(() => {
                                        that.animations[4].container()
                                        that.vue.$data.msg = that.NPC.name + "逃了出来"
                                    }, 4000)

                                }
                            }
                        },
                        {
                            name: '大师球',
                            describe: '必定捕捉野生宝可梦',
                            effects(that) {
                                that.animations[3].container()
                                that.vue.$data.msg = '...'
                                setTimeout(() => {
                                    that.vue.$data.msg = '捕捉到了' + that.NPC.name + '!'
                                    that.gameOver(that)
                                }, 4000)
                            }
                        }
                    ]
                },
                {
                    name: '宝可梦',
                    selecting: false,
                    msging: true,
                    msg() { return '暂时没有别的宝可梦~' }
                },
                {
                    name: "逃跑",
                    selecting: false,
                    msging: true,
                    msg(that) {
                        if (Math.random() >= 0.3) {
                            that.state=2
                          that.gameOver(that,1)
                            return '逃跑成功了'
                        } else {
                           that.state=2
                            return '逃跑失败了'
                        }
                    }
                }
            ],
        }
        const liyuwang = {
            name: '鲤鱼王',
            HP: 20,
            HPvalue: 20,
            defenses: 0,
            msg() { return '想要<br/>' + this.name + '做什么？' },
            selects: [
                {
                    name: '技能',
                    selecting: true,
                    msging: false,
                    container: [
                        {
                            name: '跃起',
                            describe: '跳起来。',
                            effects(that) {
                                that.vue.$data.msg = "也不攻击只是一蹦一蹦地跳，什么也没发生..."

                            }
                        },
                        {
                            name: '殊死一搏',
                            describe: '殊死一搏',
                            effects(that) {
                                that.player.HPvalue += (that.player.defenses - 3)
                                that.vue.$data.P_HPvalue = that.player.HPvalue
                                that.vue.$data.msg = "造成" + -(that.player.defenses - 3) + "点伤害"
                            }
                        }
                    ]
                },
            ],
        }

        class pokemon {
            constructor(data, vue, data2, gsap) {
                this.animations = [
                    {
                        name: "入场", container() {

                            gsap.from('.player', {
                                yoyo: true,
                                duration: 0.5,
                                bottom: '5%',

                                repeat: 2,

                                ease: 'bounce.out'
                            })
                        }
                    },
                    {
                        name: '战败', container() {
                            gsap.to('.NPC', {
                                duration: 2,
                                opacity: 0,
                            })
                            gsap.fromTo('.NPC', {
                                duration: 2,
                                ease: 'bounce.out',
                                rotateZ: '15deg',
                            }, {
                                rotateZ: '-15deg'
                            })
                        }
                    },
                    {
                        name: '胜利', container() {

                            gsap.fromTo('.player', {
                                duration: 2,
                                scale: 0.1,


                            }, {

                                rotateZ: '1800deg',
                                scale: 1
                            })

                        }
                    },
                    {
                        name: '精灵球',
                        container() {
                            document.querySelector('.qiu').style = 'display:block;'
                            gsap.from('.qiu', {
                                duration: 3,
                                rotateZ: '7200deg',

                            })
                            gsap.fromTo('.qiu', {
                                bottom: "10%",
                                left: "30%",
                            }, {

                                delay: 2,
                                top: "25%",
                                left: "76%"
                            })
                            gsap.to('.NPC', {
                                scale: 0,
                                delay: 3
                            })
                            gsap.to(".qiu", {
                                delay: 3,
                                duration: 1,
                                scale: 1.2,
                                yoyo: true,
                                repeat: 3


                            });
                        }
                    },
                    {
                        name: '捕捉失败',
                        container() {
                            gsap.to('.NPC', {
                                scale: 1,
                            })
                            console.log(1)
                            document.querySelector('.NPC').style = ''
                            document.querySelector('.qiu').style = 'display:none;'
                        }
                    }
                ]
                this.vue = vue
                this.selectsDom = document.querySelectorAll('.select')
                this.pointer = document.querySelector('.pointer')
                this.root = document.querySelector('.pokemon_')
                this.selects = [
                    { name: '战斗' },
                    { name: '背包' },
                    { name: '宝可梦' },
                    { name: '逃跑' }
                ]
                this.position = [
                    { left: 0, top: '1.5rem' },
                    { left: "50%", top: '1.5rem' },
                    { left: 0, top: "6.5rem" },
                    { left: '50%', top: "6.5rem" }
                ]
                this.state = 0
                this.player = data
                this.NPC = data2
                this.selectingIndex = 0
            }
            animation() {
                // this.animations[3].container()
                // this.animations[1].container()
            }
            //操作
            startGame() {
                let nowPosition = 0

                //键盘的操作
                this.root.addEventListener('keydown', (e) => {
                    //↓40，↑38，←37，→39
                    //如果还在对话则不执行下面动作
                    if (this.vue.$data.msging) {

                        return
                    }

                    if (e.keyCode == 37) {
                        nowPosition -= 1;
                        nowPosition < 0 && (nowPosition = this.position.length - 1)
                    }
                    if (e.keyCode == 39) {
                        nowPosition += 1;
                        nowPosition > this.position.length - 1 && (nowPosition = 0)
                    }
                    if (e.keyCode == 40) {
                        nowPosition += this.position.length / 2;
                        nowPosition > this.position.length - 1 && (nowPosition = 0)
                    }
                    if (e.keyCode == 38) {
                        nowPosition -= this.position.length / 2;
                        nowPosition < 0 && (nowPosition = this.position.length - 1)
                    }

                    this.pointer.style = `left:${this.position[nowPosition].left};top:${this.position[nowPosition].top};`
                    // e.keyCode == 13 && select(nowPosition, this)
                    //有操作时再执行更新信息
                    if (this.state) {
                        this.selectingIndex2 = nowPosition
                        let info = this.player.selects[this.selectingIndex].container[nowPosition]
                        this.vue.$data.msg = info && info.describe || '-'
                    }
                });

                //点击进入选择状态
                this.selectsDom.forEach((e, index) => {
                    e.addEventListener('click', () => {
                        nowPosition = index
                        select(index, this)
                    })

                })
                this.controlKey = () => {
                    if (this.state == 2) {
                        this.waitNPC(this)
                        return
                    }
                    this.selectingIndex2 = 0
                    this.vue.$data.msging = false;
                    this.vue.$data.selecting = false;
                    this.vue.$data.selects = this.selects;
                    this.vue.$data.msg = this.player.msg();
                    this.state = 0;
                }
                //返回的键
                document.querySelectorAll('.back').forEach(e => {
                    e.onclick = () => {
                        this.controlKey()
                    }

                })
                //确认的键
                document.querySelector('.ok').onclick = () => {


                    select(0, this)
                }
                //处理选择
                function select(selectingIndex2, that) {

                    //选择1级操作
                    let data = that.vue.$data
                    let msging = that.player.selects[selectingIndex2].msging;
                    let selecting = that.player.selects[selectingIndex2].selecting;





                    //1级级操作
                    if (!(that.state)) {

                        //升阶到2级选项
                        that.state += 1;
                        //调整对话框状态
                        data.selecting = selecting;
                        //存储选择的序号
                        that.selectingIndex = selectingIndex2
                        data.msging = msging;
                        that.selectingIndex2 = selectingIndex2
                        //判断是处于选择状态
                        if (selecting) {
                            //切换选项
                            selectingIndex2 = 0
                            data.selects = that.player.selects[that.selectingIndex].container;
                            //更新选项消息、描述

                            let info = that.player.selects[that.selectingIndex].container[selectingIndex2]

                            that.vue.$data.msg = info && info.describe || '-'
                        }
                        //判断是处于消息状态
                        if (msging) {

                            data.msg = that.player.selects[selectingIndex2].msg(that)
                        }
                        //2级操作
                    } else if (that.state) {
                        data.selecting = false;
                        data.msging = true;
                        let selectData = that.player.selects[that.selectingIndex].container[selectingIndex2]
                        console.log(selectData)
                        //调用操作的效果函数
                        selectData.effects(that)
                        that.state = 2
                    }

                }

            }
            gameOver(that,index=0) {
                console.log(1)
                
                let msgList = [that.player.name + '赢得了胜利', '感谢游玩！！！']
                that.waitNPC = () => {
                    if (index <= msgList.length - 1) {
                        that.vue.$data.msg = msgList[index]
                       
                    }
                    if(index==msgList.length){
                        setTimeout(() => {
                            that.vue.$emit('toggle')
                        }, 500);
                    }
                    index++
                }
                
            }
            waitMsg() {

            }
            waitNPC(that) {
                // that.vue.$data.msg
                let randomS = that.NPC.selects[0].container.sort(() => { return Math.random() - 0.5 })[0]
                that.vue.$data.msg = that.NPC.name + '使出了' + randomS.name

                document.querySelectorAll('.back').forEach(function (e) {
                    e.onclick = () => {
                        randomS.effects(that)
                        that.state = 0
                        e.onclick = () => {
                            that.controlKey()
                        }
                    }
                });




            }
        }
        const shuiyueyu2 = new pokemon(shuiyueyu, this, liyuwang, gsap)
        shuiyueyu2.startGame()
        shuiyueyu2.animation()
    }
}
</script>
<style scoped lang="less">
.pokemon_ {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #bbb;
    --p_fontsize: 4rem;
    font-size: var(--p_fontsize);
    text-shadow: .3rem .2rem .1rem rgba(0, 0, 0, 0.5);
    color: black;

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: fill;
    }

    .qiu {
        width: 4rem;
        height: 4rem;
        position: absolute;
        bottom: 0;
        left: 20%;
        z-index: 3;
        display: none;
    }
}

.bg {
    position: relative;
    width: 100%;
    height: 75%;
    top: 0;
    left: 0;


    .player,
    .NPC {
        position: absolute;
        width: 15%;
        // background-color: aqua;
    }

    .NPC {
        top: 25%;
        right: 18%;
        animation: Standby 1s linear;
    }

    .player {
        bottom: 0%;
        left: 20%;
    }



    .NPCInfo,
    .playerInfo {
        width: calc(10 * var(--p_fontsize));
        position: absolute;
        top: 10%;
        left: 8%;
        background-color: #fffbdf;
        border-radius: var(--p_fontsize) 0 var(--p_fontsize) 0;
        border: .25rem #1f3607 solid;
        outline: .25rem #506760 solid;
        padding: .5rem 2rem;
        box-sizing: border-box;
        display: grid;
        grid-template: 1fr auto/1fr;
        z-index: 1;

        &>div:nth-of-type(1) {
            display: flex;

            p:nth-of-type(2) {
                margin-left: auto;

            }
        }

        &>div:nth-of-type(2) {
            background-color: #50685a;
            display: flex;
            width: 60%;
            margin-left: auto;
            align-items: center;
            justify-content: center;
            gap: .5rem;
            color: #fc9074;
            font-size: 1.5rem;
            border-radius: 1rem;
            line-height: 1.8rem;

            .HPvalue {
                box-sizing: border-box;
                border: #50685a solid .25rem;
                box-shadow: inset #fffbdf .25rem .25rem 0, inset #fffbdf -.25rem -.25rem;
                width: calc(100% - var(--p_fontsize) * 1.5);
                // background-color: #6ff1a4;
                // background:linear-gradient(#6ff1a4 70%,#4b3e58 30);
                height: 100%;
                border-radius: .5rem;

                div {
                    margin-top: .25rem;
                    margin-left: .25rem;
                    height: calc(100% - .5rem);
                    width: calc(100% - .5rem);
                    background-color: #6ff1a4;

                }
            }
        }
    }

    .playerInfo {
        left: 60%;
        top: 70%;
        grid-template: 1fr auto auto/1fr;

        div:nth-of-type(3) {
            margin-left: auto;
            font-size: 2rem;
        }
    }
}

.title {
    color: #fffaff;
    height: 25%;
    width: 100%;
    background-color: #4b3f54;
    box-sizing: border-box;
    display: flex;
    gap: .3rem;
    padding: .5rem;

    .msg {
        flex-grow: .55;
        height: 100%;
        border-left: 3rem #da483a solid;
        border-top: .5rem #da483a solid;
        border-bottom: .5rem #da483a solid;
        background-color: #da483a;
        border-radius: 2rem 0 0 2rem;
        box-sizing: border-box;
        position: relative;

        p {
            width: 100%;
            height: 100%;
            box-sizing: border-box;
            background-color: #689fa5;
            border-radius: 2rem 0 0 2rem;
            padding: 1rem;
            box-shadow: inset 0 0 .4rem rgba(0, 0, 0, 0.2);
        }

        //确定键
        span {
            position: absolute;
            top: 50%;
            right: var(--p_fontsize);
            display: flex;

            img {
                margin-left: 2rem;
                width: var(--p_fontsize) !important;
                height: var(--p_fontsize) !important;
            }
        }
    }

    .msgFull {
        flex-grow: 1;
        border-right: 3rem #da483a solid;
        border-radius: 2rem;

        p {
            border-radius: 2rem;
        }
    }

    .selectBar {
        flex-grow: .45;
        display: flex;

        .selectBar1,
        .selectBar2 {
            box-shadow: 0rem 0rem .5rem rgba(0, 0, 0, .3);
            color: #4a474b;
            flex-grow: 1;
            height: 100%;
            border: #2c2a35 solid .5rem;
            border-radius: .5rem;
            box-sizing: border-box;
            background-color: #776a86;


            &>div {
                border-radius: .5rem;
                margin: 1rem;
                height: calc(100% - 2rem);
                width: calc(100% - 2rem);
                background-color: #fffaff;
                display: grid;
                grid-template: 1fr 1fr / 1fr 1fr;
                position: relative;

                //每一项
                .select {
                    padding-left: var(--p_fontsize);

                    P {
                        cursor: pointer;
                        width: fit-content;
                    }
                }

                .pointer {
                    position: absolute;
                    //第二列的位置
                    // left: 50%;
                    left: 0rem;
                    height: 3rem;
                    width: 3rem;
                    top: 1.5rem;
                    //第二行的位置
                    // top: 7.5rem;
                    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%);
                    clip-path: polygon(0 0, 100% 0, 100% 100%);
                    background-color: #4a474b;
                    transform: rotateZ(45deg);
                    filter: drop-shadow(10rem 10rem 0 #2c2a35);
                }
            }
        }

    }

    .selectBarFull {
        flex-grow: 1;
        gap: 1rem;

        .selectBar1 {
            flex-grow: .9;
        }

        .selectBar2 {
            flex-grow: .1;

            &>div {
                display: flex;
                place-items: center;
            }

            div p:nth-of-type(1) {
                flex-grow: 1;
                margin-left: 1rem;

            }

            div p:nth-of-type(2) {
                margin-left: auto;
                margin-right: var(--p_fontsize);
                display: flex;
            }
        }
    }

    .hidden {
        display: none;
    }
}

@keyframes Standby {
    25% {
        transform: translate(0, -10%);
    }

    50% {
        transform: translate(0, 10%);

    }
}

// 动画还是用gsap吧。
</style>