<template>
    <header>
        <div id="pageJump">
            <ul><li style="--i:1">没</li><li style="--i:2">有</li><li style="--i:3">更</li><li style="--i:4">多</li><li style="--i:5">页</li><li style="--i:6">面</li><li style="--i:7">了</li></ul>
            <button @click="pageJumpBtn(-1,$event)" :class="{'runway': run1}" title="返回上一页">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24" style="--i:1">
                    <path fill="none" d="M0 0h24v24H0z"></path>
                    <path fill="currentColor"
                        d="M1.946 9.315c-.522-.174-.527-.455.01-.634l19.087-6.362c.529-.176.832.12.684.638l-5.454 19.086c-.15.529-.455.547-.679.045L12 14l6-8-8 6-8.054-2.685z">
                    </path>
                </svg>
            </button>
            <button @click="pageJumpBtn(1,$event)" :class="{'runway': run2}" title="返回下一页">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24" style="--i:-1">
                    <path fill="none" d="M0 0h24v24H0z"></path>
                    <path fill="currentColor"
                        d="M1.946 9.315c-.522-.174-.527-.455.01-.634l19.087-6.362c.529-.176.832.12.684.638l-5.454 19.086c-.15.529-.455.547-.679.045L12 14l6-8-8 6-8.054-2.685z">
                    </path>
                </svg>
            </button>
        </div>
        <p>
            keyAnime动漫
        </p>
        <searchBar :isShow="isShow"></searchBar>
    </header>
</template>
<script>
import searchBar from '../page/rightBar/searchBar.vue';
export default {
    name: 'headerBar',
    props:['isShow'],
    components:{
        searchBar
    },
    data(){
        return{
            run1: false,
            run2: false,
            jumpTime: ""
        }
    },
    methods: {
        pageJumpBtn(target,e){
            if(!this.run1 && !this.run2){
                target == 1 && (this.run2 = true) || (this.run1 = true);
                setTimeout(() => {
                    this.$router.go(target);
                    this.jumpTime = document.querySelector("#pageJump ul") && setTimeout(() => {
                        this.run1 = false;
                        this.run2 = false;
                        (document.querySelector("#pageJump ul").style="display: none");
                    },3750)
                    document.querySelector("#pageJump ul").style="display: flex; left: " + (e.screenX - 112) + "px";
                },250)
            }
        }
    },
    beforeDestroy(){
        clearTimeout(this.jumpTime);
    }
}
</script>
<style scoped lang="less">
// 无法跳转网页时提示
#pageJump ul{
    position: fixed;
    display: none;
    flex-direction: row-reverse;
    animation: tipsbox 3s .25s;
    top: -2em;
}
#pageJump ul li{
    position: relative;
    animation: tips .5s calc(var(--i) * .25s) 2 alternate;
}
@keyframes tipsbox{
    from {padding-left: 0;}
    to {padding-left: 7em;}
}
// 每个文字动画
@keyframes tips{
    from {opacity: 0;transform: scale(.5);bottom: -1.5em;}
    to {opacity: 1;transform: scale(1.25);bottom: -3.5em;}
}

// 纸飞机跑道维持现状
.runway{
    animation: fly-1 0.6s ease-in-out infinite alternate !important;
    flex: 75% 0 0 !important;
    background-size: 55% 100% !important;
    animation: fly-1 0.6s ease-in-out infinite alternate;
}
.runway svg{
    animation: jump 4s;
}
// 纸飞机射出动画，返回动画一般不会被播放出来
@keyframes jump{
    0% {right: 0;opacity: 1;transform: rotateY(calc(var(--i) * 90deg + 90deg)) rotateZ(45deg) scale(1.1);}
    16% {right: calc(var(--i) * 4.5vw);opacity: 0;transform: rotateY(calc(var(--i) * 90deg + 90deg)) rotateZ(45deg) scale(.5);}
    17% {right: calc(var(--i) * -1rem);opacity: 0;transform: rotateY(calc(var(--i) * 90deg + 90deg)) rotateZ(45deg) scale(.5);}
    75% {right: calc(var(--i) * -1rem);opacity: 0;transform: rotateY(calc(var(--i) * 90deg + 90deg)) rotateZ(45deg) scale(.5);}
    100% {right: 0;opacity: 1;transform: rotateY(calc(var(--i) * 90deg + 90deg)) rotateZ(45deg) scale(1.1);}
}
 header {
        display: flex;
        align-items: center;
        padding: 0 2%;

        img {
            background-color: var(--bgDeep);
            height: 4rem;
        }
        #pageJump{
            width: 10%;
            // margin-right: 30%;
            display: flex;
            justify-content: center;

            button {
                flex: 25% 0 0;
                background: var(--buttonBg);
                color: var(--fontColor);
                cursor: pointer;
                transition: flex .5s,background-size .5s .5s;
                background: linear-gradient(var(--fontColor) 5%,transparent 5%,transparent 95%,var(--fontColor) 95%) no-repeat;
                background-size: 100% 100%;
                border: none;
                outline: none;

                &:last-child{
                    background-position: right;
                }

                &:hover {
                    color: var(--hovercolor);
                    flex: 75% 0 0;
                    background-size: 55% 100%;
                    animation: fly-1 0.6s ease-in-out infinite alternate;
                }

                svg{
                    transition: transform .25s,right .25s;
                    transform: rotateY(calc(var(--i) * 90deg + 90deg)) rotateZ(0) scale(1);
                    position: relative;
                    right: 0;
                }
                &:hover svg{
                    transform: rotateY(calc(var(--i) * 90deg + 90deg)) rotateZ(45deg) scale(1.1);
                    filter: drop-shadow(0 .5em .5em var(--fontColor));
                }
                @keyframes fly-1 {
                    from {
                        transform: translateY(0.1em);
                    }

                    to {
                        transform: translateY(-0.1em);
                    }
                }
            }
        }
        p {
            flex-grow: 1;
            white-space: nowrap;
            text-align: center;
        }
    }

</style>