<template>
    <div :class="`container ${classToggleB ? 'right-panel-active' : ''}`">
        <div class="container__form container--signup">
            <form action="#" class="form" id="form1">
                <h2 class="form__title" @click="">注 册</h2>
                <input type="text" placeholder="用户名" class="input" v-model="regUesrName" />
                <input type="email" placeholder="邮箱" class="input" v-model="regEmail" />
                <input type="password" placeholder="密码" class="input" v-model="regUesrPwd" />
                <button class="btn" @click="register($event)">注 册</button>
            </form>
        </div>
        <div class="container__form container--signin">
            <form action="#" class="form" id="form2">
                <h2 class="form__title">登 录</h2>
                <input type="text" placeholder="用户名" class="input" v-model="uesrName" />
                <input type="password" placeholder="密码" class="input" v-model="uesrPwd" />
                <a href="#" class="link" style="visibility: hidden;">忘记密码?</a><button class="btn"
                    @click="logoin($event)">登录</button>
            </form>
        </div>
        <div class="container__overlay">
            <div class="overlay">
                <div class="overlay__panel overlay--left">
                    <button class="btn" id="signIn" @click="classToggle(false)">登 录？</button>
                </div>
                <div class="overlay__panel overlay--right">
                    <button class="btn" id="signUp" @click="classToggle(true)">注 册？</button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Message from '@yaohaixiao/message.js/message'
export default {
    name: 'logoinForm',
    data() {
        return {
            classToggleB: true,
            regUesrName: '',
            regUesrPwd: '',
            regEmail: '',
            uesrName: "",
            uesrPwd: '',
            uesrList: JSON.parse(localStorage.getItem('uesrList')) || []
        }
    },
    components: {
        
    },
    methods: {
        classToggle(arg) {
            this.classToggleB = arg;
        },
        register(e) {
            e.preventDefault()
            if ([this.regUesrName, this.regUesrPwd, this.regEmail].some(e => e == '')) {
                Message.error({
                    effect: 'plain',
                    message: '请填写信息'
                })
                return 0;
            }
            if (this.uesrList.map(e => e.name).some(e => e == this.regUesrName)) {
                Message.error({
                    effect: 'plain',
                    message: '该用户名已被注册'
                })
                return 0;
            }

            let regInfo = { name: this.regUesrName, pwd: this.regUesrPwd, Email: this.regEmail, state: false ,collectList: []}
            this.uesrList.push(regInfo)
            localStorage.setItem("uesrList", JSON.stringify(this.uesrList))
            Message.success({
                round: true,
                message: '注册成功！'
            })
        },
        logoin(e) {
            e.preventDefault()
            if ([this.uesrName, this.uesrPwd].some(e => e == '')) {
                Message.error({
                    effect: 'plain',
                    message: '请填写账号或密码'
                })
                return 0;
            }
            if (this.uesrList.every(e => { return e.name != this.uesrName })) {
                Message.error({
                    effect: 'plain',
                    message: '用户名不存在'
                })
                return 0;
            }
            let logoinUesr = this.uesrList.filter(e => e.name == this.uesrName)
            if (logoinUesr[0].pwd == this.uesrPwd) {
                this.uesrList.forEach(e => {
                    if (e.name == this.uesrName) {
                        e.state = true;
                        Message.success({
                            round: true,
                            message: '登录成功，即将跳转主页...'
                        })
                        localStorage.setItem("uesrList", JSON.stringify(this.uesrList))
                        localStorage.setItem('logoinUesr',JSON.stringify( logoinUesr[0]))
                        this.$store.commit('setLogoinUesr', logoinUesr[0])
                        // console.log(this.$store.state)
                        setTimeout(() => {
                            this.$router.push('./index')
                        }, 5000);
                    }
                });
            } else{
                Message.error({
                    effect: 'plain',
                    message: '密码错误'
                })
            }

        }
    },
    mounted() {
        console.log()
        // Message.info({
        // 	duration: 5,
        // 	message: 'message.js - 一个小巧实用的 JavaScript 提示信息工具库。'
        // })
        // Message.success({
        // 	round: true,
        // 	message: 'message.js - 一个小巧实用的 JavaScript 提示信息工具库。'
        // })
        // console.log(Message)
    }
}
</script>
<style scoped>
@import '../../../../node_modules/@yaohaixiao/message.js/message.min.css';



button,
input {
    overflow: visible;
    color: var(--fontColor);
}

a {
    background-color: transparent;
    -webkit-text-decoration-skip: objects
}

a:active,
a:hover {
    outline-width: 0
}

button,
input {
    font-family: sans-serif;
    font-size: 100%;
    line-height: 1.15;
    margin: 0
}

[type=submit],
[type=reset],
button,
html [type=button] {
    appearance: button;
    -webkit-appearance: button;
}

[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner,
button::-moz-focus-inner {
    border-style: none;
    padding: 0
}

[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring,
button:-moz-focusring {
    outline: ButtonText dotted 1px
}

::-webkit-file-upload-button {
    -webkit-appearance: button;
    font: inherit
}

.form__title {
    font-weight: 300;
    margin: 0;
    margin-bottom: 1.25rem
}

.link {
    color: var(--gray);
    font-size: .9rem;
    margin: 1.5rem 0;
    text-decoration: none
}

.container {
    background-color: var(--bgWihte);
    border-radius: var(--button-radius);
    box-shadow: 0 .9rem 1.7rem rgba(0, 0, 0, 0.25), 0 .7rem .7rem rgba(0, 0, 0, 0.22);
    height: var(--max-height);
    max-width: var(--max-width);
    overflow: hidden;
    position: relative;
    width: 100%
}

.container__form {
    height: 100%;
    position: absolute;
    top: 0;
    transition: all .6s ease-in-out
}

