    font-weight: bold;
    letter-spacing: .1rem;
    padding: .9rem 4rem;
    text-transform: uppercase;
    transition: transform 80ms ease-in
}

.form>.btn {
    margin-top: 1.5rem
}

.btn:active {
    transform: scale(0.95)
}

.btn:focus {
    outline: 0
}

.form {
    background-color: var(--bgWihte);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 3rem;
    height: 100%;
    text-align: center
}

.input {
    background-color: var(--bglight);
    border: 0;
    padding: .9rem .9rem;
    margin: .5rem 0;
    width: 100%
}

@-webkit-keyframes show {

    0%,
    49.99% {
        opacity: 0;
        z-index: 1
    }

    50%,
    100% {
        opacity: 1;
        z-index: 5
    }
}

@keyframes show {

    0%,
    49.99% {
        opacity: 0;
