<template>
    <div id='showUesr'>
        <div class="msg">
            <h4>
                hi~{{ uesrInfo.name }}

            </h4>
            <h4 @click="logoout">
                退出登录
            </h4>
        </div>
        <p>欢迎来到这个专为动漫爱好者打造的独特平台！我们是一个充满热情的团队，致力于为广大动漫迷提供最新、最全面、最优质的动漫资讯和资源。自成立以来，我们始终坚持以用户为中心的原则，努力打造一个温馨、友好的交流环境。

            在这个充满活力的社区，您可以随时获取到最新的动漫作品信息、了解最热门的动漫人物、分享最有趣的话题，还可以与其他志同道合的朋友一起共同探讨、共同成长。无论您是喜欢热血动漫、恋爱动漫，还是其他各种类型的动漫作品，我们都将竭诚为您带来丰富多彩的内容。

            我们的团队由一群热爱动漫、有着共同兴趣爱好的人组成，他们来自不同的领域和背景，但共同的目标是通过这个平台分享动漫的快乐和魅力。我们时刻关注着动漫界的最新动态和潮流，以确保我们提供的内容始终保持新颖和吸引力。

            此外，我们也非常重视用户的需求和反馈。如果您有任何建议或意见，都可以随时与我们联系，我们将竭尽全力满足您的期望。我们希望通过您的参与和支持，让这个平台变得更加完善、更加繁荣。</p>
        <div class="about">

            <div class="imgBox">
                <img src="/img/about/image1.jpg">

            </div>
            <div class="imgBox">
                <img src="/img/about/image2.jpg">
            </div>
            <div class="imgBox">
                <img src="/img/about/image3.jpg">
            </div>
            <div class="imgBox">
                <img src="/img/about/image4.jpg">
            </div>
            <div class="imgBox">
                <img src="/img/about/image5.jpg">
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'showUesr',
    computed: {
        uesrInfo() {
            return this.$store.state.logoinUesr
        }
    },
    methods: {
        logoout() {
            localStorage.removeItem('logoinUesr');
            this.$router.go(0);
            // YQ - 筛选当前用户更改其收藏列表
            let collectData = JSON.parse(localStorage.getItem("uesrList")).map(item => {
                if (item.name == this.$store.state.logoinUesr.name) {
                    return this.$store.state.logoinUesr;
                } else {
                    return item;
                }
            });
            // 对localStorage中当前用户存储收藏列表更新
            localStorage.setItem("uesrList", JSON.stringify(collectData));
        }
    },
    mounted() {
        var about = document.querySelector('.about')
        var time = 0
        setInterval(() => {
            time++

            about.style = `transform: translateX(-${time * 20}%);transition: 1s all; `;
            if (time == 4) {
                time = 0

            }
        }, 1500)
    }
}

</script>
<style scoped lang="less">
#showUesr {
    position: relative;
    height: 100%;
    width: 100%;
    overflow: hidden;

    p {
        // width: 20%;
        position: absolute;
        z-index: 3;
        color: black;
        bottom: 5%;
        box-sizing: border-box;
        padding: 0 4rem;
        font-size: 1.5rem;
        background-color: var(--shadowColor);
    }
}

.msg {
    position: absolute;
    top: 5%;
    left: 10%;
    border-radius: 1rem;
    transform: translateX(-50%);
    background-color: var(--gray);
    padding: 1rem 5rem;
    z-index: 4;
}

.about {
    overflow-y: auto;
    width: 500%;
    height: 100%;
    display: flex;

    .imgBox {


        width: 20%;
        height: 100%;



        img {

            width: 100%;
            height: 100%;
            object-fit: fill;
        }
    }


}
</style>