<template>
    <div class="searchBar" :class="{ 'show': isShow }">
        <div>
            <input type="text" v-model="searchText" placeholder="搜索动漫" @keydown.enter="addHistory">
            <div @click="addHistory"><ion-icon name="search-outline"></ion-icon></div>
            <div>
                <ul>
                    <li><b>搜索记录</b></li>
                    <li v-for="name in history" :key="name" class="historyItem" @click="addHistory('a', name)">
                        {{ name }}<ion-icon name="close-circle-outline" @click="delHistory(name)"></ion-icon>
                    </li>
                    <li v-show="!history.length" class="recommend" style="font-size: .8rem;">
                        <p>您还未使用过搜索功能，是难以抉择吗？这样的话，这个动漫如何？</p>
                        <p @click="searchText = RandomAnime; addHistory('a', RandomAnime);">——{{ RandomAnime }}</p>
                    </li>
                    <li v-show="history.length" class="recommend" @click="cleraHistory">
                        清空记录
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: "searchBar",
    data() {
        return {
            history: JSON.parse(localStorage.getItem("history")) || [],
            searchText: '',
            RandomAnime: ''
        }
    },
    computed: {

    },
    props: ['isShow', 'searching'],
    methods: {
        addHistory(a, name) {
            if (this.searchText != '' && !this.history.includes(this.searchText)) {
                this.history.push(this.searchText)
                localStorage.setItem("history", JSON.stringify(this.history))
                this.$router.push({ path: '/search', query: { name: this.searchText } })

            } else if (name) {
                console.log(name)
                this.$router.push({ path: '/search', query: { name } })
            }
        },
        delHistory(name) {
            this.history = this.history.filter((e) => {
                return !(e === name)
            })
            localStorage.setItem("history", JSON.stringify(this.history))
        },
        cleraHistory() {
            this.history = []
            localStorage.clear()
        },
        getRandomAnime() {
            let state = this.$store.state
            return [...state.filmAnimeList, ...state.tvAnimeList].sort(() => { return Math.random() - 0.5 })[0].name
        }
    },
    mounted() {
        if (this.searching) {
            this.searchText = this.searching
        }
        this.RandomAnime = this.getRandomAnime()
    },
    beforeDestroy() {

    }
}
</script>
<style scoped lang="less">
.show {
    visibility: hidden;
    width: 10% !important;
}

form {
    position: relative;
    display: flow-root;
}

.searchBar {
    width: 40%;
    height: 4rem;
    padding: 0 1%;
    display: flex;
    box-sizing: border-box;
    justify-content: space-between;
    border-bottom: 1px solid var(--backColorDeepBorder);

    &>div {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        position: relative;

        input {
            flex: 1;
            padding-left: 1rem;
            outline: none;
            border: none;
            background-color: var(--bgWihte);
            height: 5vh;
            border-radius: 1rem 0 0 1rem;
            box-shadow: var(--shadowColor) 1px 1px 1px;
            color: var(--fontColor);
        }

        //搜索功能
        div:nth-of-type(1) {
            height: 5vh;

            aspect-ratio: 1;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 0 1rem 1rem 0;
            background-color: var(--bgWihte);
            box-shadow: var(--shadowColor) 1px 1px 1px;
        }

        // 搜索记录
        div:nth-of-type(2) {
            width: 100%;
            z-index: 6;
            position: absolute;
            top: 6.4vh;
            background-color: var(--bgWihte);
            transition: .5s all;
            // display: none;
            max-height: 0;
            max-width: 0%;
            // max-height: 20vh;
            // max-width: 100%;
            overflow: hidden;

            &:hover {
                max-height: 30vh;
                height: 30vh;
                max-width: 100%;

            }

            ul {
                padding: 1rem .5rem;
                height: 100%;
                position: relative;

                //搜索记录每一项
                li {
                    width: 100%;

                }

                //标题语
                &>li:first-of-type {
                    margin-bottom: 2rem;
                    width: 5rem;
                    text-align: center;
                    border-bottom: 1px solid var(--shadowColor);
                }

                .historyItem {
                    display: inline-flex;
                    align-items: center;
                    width: auto;
                    border-radius: .5rem;
                    // background-color: var(--backColorDeep1);
                    margin: 0 0.2rem;
                    padding: 0.5rem;
                    border: 1px solid var(--shadowColor);
                }

                //推荐语
                .recommend {
                    width: 100%;
                    display: flex;
                    align-items: flex-end;
                    text-overflow: clip;
                    flex-direction: column;
                    margin-top: auto;
                    margin-bottom: 1.5rem;
                    position: absolute;
                    bottom: 1.5rem;
                    right:.5rem;
                    p:nth-of-type(2){
                        cursor: pointer;
                    }
                }
            }
        }

        input:focus~div:nth-of-type(2) {
            max-height: 30vh;
            height: 30vh;
            max-width: 100%;


        }
    }
}

&>li:nth-of-type(3) {
    height: 3vh;
    margin-top: 1.5vh;
    display: flex;
    justify-content: flex-end;

    div {
        padding: 0 2%;

        &:not(:first-of-type) {

            border-left: 1px solid black;
        }
    }
}</style>