<template>
    <div id='search'>
        <headerBar :isShow="true"></headerBar>
        <div class="searchContent">
            <div>
                <searchBar style="width: 80%;margin: 5% 10% 0;" :searching="searchText"></searchBar>
                <h2>相关内容</h2>
                <div :class="{max:infoList.length>=5,min:infoList.length<=5}">
                    <item v-for="i in infoList " :key="i.name" :info='i'></item>
                </div>
                <h3 v-if="!infoList.length">暂无相关信息,换一个搜索词吧！</h3>
            </div>
        </div>
    </div>
</template>
<script>
import headerBar from '../content/headerBar.vue';
import searchBar from './rightBar/searchBar.vue';
import item from '../content/item.vue';
export default {
    name: 'search',
    components: {
        headerBar,
        searchBar,
        item
    },
    data(){
        return {
            infoList:[]
        }
    },
    computed:{
        searchText(){
            return this.$route.query.name
        }
    },
    mounted(){
        
        this.infoList=[...this.$store.state.tvAnimeList,...this.$store.state.filmAnimeList].filter((e)=>{
            return !(e.name.indexOf(this.searchText))
        })
        console.log(this.infoList.length)
    }
}
</script>
<style scoped lang="less">
.searchContent {
    height: calc(100svh - 4.1rem);
    overflow-y: auto;
    border: .01rem var(--shadowColor);
    border-style: groove none none outset;
    border-radius: .8rem 0 0 0;
    padding: 0 1%;
    display: flex;
    box-sizing: border-box;
    & > div{
        width: 100%;
        display: flex;
        flex-direction: column;
        h2{ 
            margin:2rem 0 2rem;
            text-align: center;
        }
        //数量大于六个时
        .max ,.min{
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            gap: 1rem;
        }
        //小于六个时
        .min {
            justify-content: start;
        }
        h3{
            display: flex;
            justify-content: center;
            align-items: center;
            flex-grow: .6;
        }
    }
}
</style>