<template>
    <div id="curtain">
        <div class="zipper">
            <hr>
            <img src="./catpaw.png" alt="猫猫拳" draggable="false" @click="toggle()">
        </div>
        <main>
            <ul>
                <li style="--i:0" class="toggleA3"></li>
                <li style="--i:1" class="toggleA3"></li>
            </ul>
            <!-- <nav style="display: flex;justify-content: center;align-items: center;color: #FFF;font-size: 2rem;font-family: '华文彩云';">这里将展示内容</nav> -->
            <pokemon_ @toggle="toggle"/>
        </main>
    </div>
</template>
<script>
import Message from '@yaohaixiao/message.js/message'
import pokemon_ from './pokemon_.vue';
export default {
    name: 'stage',
    data() {
        return {
            clickAccording: true,
            toggleAccording: true
        }
    },
    components: {
        pokemon_
    },
    methods: {
        toggle() {
            if (this.clickAccording) {
                this.toggleAccording = !this.toggleAccording;
                // 帷幕垂下
                document.querySelector("#curtain main").classList.toggle("toggleH");
                // 猫猫拳畏缩
                document.querySelector(".zipper").classList.toggle("toggleA1");
                // 帷幕展开与谢幕
                document.querySelectorAll("#curtain main ul li").forEach(item => {
                    if (this.toggleAccording) {
                        setTimeout(() => {
                            item.classList.toggle("toggleA3");
                        })
                        item.classList.toggle("toggleA2");
                    } else {
                        item.classList.toggle("toggleA3");
                        setTimeout(() => {
                            item.classList.toggle("toggleA2");
                        })
                    }
                })
                this.toggleAccording || Message.success({
                            round: true,
                            message: '欢迎来到宝可梦世界，再次点击猫爪谢幕'
                        })
                setTimeout(() => {
                    this.toggleAccording && this.$router.go(0);
                    this.clickAccording = true;
                }, 2200)
            }
            this.clickAccording = false;
        }
    }
}
</script>
<style>
#curtain {
    position: absolute;
    width: 100%;
    z-index: 7;
}

.zipper {
    height: 20vmin;
    position: absolute;
    right: 8%;
    top: -.5rem;
    z-index: 3;
    animation: zhuan 5s infinite alternate ease-in-out .25s;
    transform-origin: top center;
    transition: all .5s;
}

.zipper hr {
    width: 1rem;
    height: 100%;
    outline: none;
    border: none;
    background: linear-gradient(35deg, transparent 33.5%, #000 33.5%, #000 66.5%, transparent 66.5%), linear-gradient(90deg, transparent 8%, #Cab0a6 8%, #Cab0a6 92%, transparent 92%);
    background-size: 100% 25%;
    transition: background-size .5s;
}

.zipper img {
    background-color: transparent;
    position: absolute;
    width: 4rem;
    height: 4rem;
    object-fit: fill;
    padding: 0;
    transform: rotateZ(180deg) scale(1);
    bottom: -3rem;
    right: -1.5rem;
    cursor: pointer;
    transition: all .5s;
}

@keyframes zhuan {
    0% {
        transform: rotateZ(0deg);
    }

    25% {
        transform: rotateZ(25deg);
    }

    50% {
        transform: rotateZ(-25deg);
    }

    75% {
        transform: rotateZ(20deg);
    }

    100% {
        transform: rotateZ(-15deg);
    }
}

/* 利用has选择器做到的父元素和兄长元素过渡 */
.zipper:has(>img:hover) {
    animation-play-state: paused;
}

/* 绳索紧绷效果 */
.zipper hr:has(+img:hover) {
    transform: scaleX(.8);
    background-size: 100% 22%;
}

/* 猫猫拳握紧 */
.zipper img:hover {
    transform: rotateZ(180deg) scale(.9);
    filter: drop-shadow(0 0 .5rem #000);
}

#curtain main {
    width: 100%;
    height: 0rem;
    transition: height 1s 1.2s;
    position: relative;
    overflow: hidden;
}

#curtain main ul {
    width: 100%;
    height: 100%;
    list-style: none;
    position: absolute;
    display: grid;
    grid-template-columns: 1fr 1fr;
    z-index: 10;
    pointer-events: none;
}

#curtain main nav {
    background-color: #000;
}

/* toggle区 */
.toggleA1 {
    animation: none !important;
    height: 10vmin !important;
}

.toggleH {
    height: 100vh !important;
    transition: height 1s !important;
}

.toggleA2 {
    animation: ini 1s 1 both 1s normal;
}

.toggleA3 {
    animation: ini 1s 1 both 0s reverse;
}

/* 幕布区块 */
#curtain main ul li {
    background: url(./红色幕布.jpg) left top / 100% 100% no-repeat;
    transform: rotateY(calc(var(--i) * 180deg));
    transition: all 1s;
    background-position: left top;
    height: 95vh;
    clip-path: ellipse(150% 150% at 0 0);
    background-size: 100% 100%;
}

#curtain main ul li::before {
    content: "";
    position: relative;
    display: block;
    left: 0;
    width: 5rem;
    height: 100vh;
    background: url(./红色幕布.jpg) left top / 50vw 100% no-repeat;
}

#curtain main nav {
    width: 100%;
    height: 95%;
}

@keyframes ini {
    from {
        height: 100%;
        clip-path: ellipse(150% 150% at 0 0);
        opacity: 1;
        background-size: 100% 100%;
    }

    to {
        height: 98%;
        clip-path: ellipse(40% 110% at 0 0);
        opacity: 0;
        background-size: 50% 100%;
    }
}</style>