<template>
    <div id='item' @click="go(info2)">
        <div class="imgBox">
            <img :src="info2.imgUrl" loading="lazy">
            <div class="buu"></div>
        </div>
        <div class="title">
            <p>{{ info2.name }}</p>
            <small>共{{ info2.site }}集</small>
            <svg @click="star($event)" :class="{'collectStar collectStarAnimate':collectIni}" fill="none" stroke="var(--fontColor)" stroke-width="1.5" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg" aria-hidden="true">
                <path stroke-linecap="round" stroke-linejoin="round" d="M11.48 3.499a.562.562 0 011.04 0l2.125 5.111a.563.563 0 00.475.345l5.518.442c.499.04.701.663.321.988l-4.204 3.602a.563.563 0 00-.182.557l1.285 5.385a.562.562 0 01-.84.61l-4.725-2.885a.563.563 0 00-.586 0L6.982 20.54a.562.562 0 01-.84-.61l1.285-5.386a.562.562 0 00-.182-.557l-4.204-3.602a.563.563 0 01.321-.988l5.518-.442a.563.563 0 00.475-.345L11.48 3.5z"></path>
            </svg>
        </div>
    </div>
</template>
<script>
import Message from '@yaohaixiao/message.js/message'
export default {
    name: 'item',
    props:['info'],
    computed:{
        info2(){
            if (this.info==undefined) {
                // 给一个默认值，方便调试
                return this.$store.state.tvAnimeList[0]
            }else {
                return this.info
            }
        },
        // YQ - 收藏初始化
        collectIni(){
            if(localStorage.getItem("logoinUesr")){
                return this.$store.state.logoinUesr.collectList.some(item => item[0] == this.info2.name)
            }else{
                // 没登陆默认没有收藏
                return false
            }
        }
    },
    methods:{
        go(info){
            this.$store.commit('goPlay',{router:this.$router,info})
        },
        // YQ - 收藏动画
        star(e){
            e.stopPropagation();
            // 判断是否登陆
            if(localStorage.getItem("logoinUesr")){
                // 获取需要收藏的属性
                let collectElement = [this.info2.name,this.info2.imgUrl,this.info2.introduce,this.info2.site];
                // 依据收藏时的class名称判断是否被收藏
                if(e.target.classList.contains("collectStar")){
                    // 移除收藏时的样式
                    e.target.classList.remove("collectStar","collectStarAnimate");
                    // 移除用户收藏
                    this.$store.state.logoinUesr.collectList = this.$store.state.logoinUesr.collectList.filter(item => item[0] !== collectElement[0]);
                    localStorage.setItem("logoinUesr", JSON.stringify(this.$store.state.logoinUesr));
                }else{
                    // 添加收藏样式
                    e.target.classList.add("collectStar","collectStarAnimate");
                    // 添加用户收藏^_^看这里是帅逼:)
                    this.$store.state.logoinUesr.collectList.push(collectElement);
                    localStorage.setItem("logoinUesr", JSON.stringify(this.$store.state.logoinUesr));
                }
            }else{
                Message.error({
                    effect: 'plain',
                    message: '请先登录'
                })
            }
        }
    },
    mounted(){
        // let b= "@"+"/img/TV/境界的彼方.jpg"
        // let a =  require(b)
        // console.log( b)
    }
}
</script>
<style scoped>
.hidden {
    display: none;
}
#item {
    width: calc(100% / 6.5);
    background-color:var(--bgWihte);
    padding: .3rem;
    border-radius:.5rem ;
    box-sizing: border-box;
}

.imgBox {
    width: 100%;
    aspect-ratio: 9/12;
    overflow: hidden;
    position: relative;
    cursor: pointer;
    border-radius:.5rem .5rem 0 0;
    overflow: hidden;
}

img {
    width: 100%;
    height: 100%;
    transition: all .8s;
    object-fit: fill;
    border-radius:.5rem .5rem 0 0;
}
.buu {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 200%;
    height: 20%;
    transform-origin: center;
    transform: rotateZ(45deg) translate(-50%, -200%);
    background-color: rgba(255, 255, 255, .25);
}

@keyframes buu {
    0% {}

    100% {
        transform: rotateZ(45deg) translate(-50%, 2000%);
    }
}

.imgBox:hover img {
    transform: scale(1.02);
}
.imgBox:hover .buu{
    animation: buu 1.5s ;
}
.title{
    margin-top: .5rem;
    display: grid;
    grid-template: auto auto / auto 1.5rem;
    grid-auto-flow: column;
    align-items: center;
}
.title small {
    font-size: 0.7rem;
}
/* 收藏按钮 */
.title svg{
    grid-row: 2 span;
    cursor: pointer;
    position: relative;
    right: 25%;
    transition: transform .25s;
}
.title svg path{
    pointer-events: none;
}
.title svg:hover{
    transform: scale(1.25);
}
/* 收藏后的样式 */
.collectStar{
    fill: yellow;
    stroke: yellow;
}
.collectStarAnimate{
    animation: starRotate .25s alternate 2;
}
@keyframes starRotate{
    from {transform: scale(1.25) rotateZ(0deg);}
    to {transform: scale(1.25) rotateZ(-90deg);}
}
</style>