<template>
    <div id='barContent'>
        <h3>热门{{ $route.meta.msg }}</h3>
        <div class="hotBar">
            <item v-for="i in randomAnimeList()" :key="i.name" :info=i />
        </div>
        <h3>恋爱{{ $route.meta.msg }}</h3>
        <div class="hotBar">
            <item v-for="i in randomAnimeList()" :key="i.name" :info=i />
        </div>
        <h3>异世界{{ $route.meta.msg }}</h3>
        <div class="hotBar">
            <item v-for="i in randomAnimeList()" :key="i.name" :info=i />
        </div>
        <h3>奇幻{{ $route.meta.msg }}</h3>
        <div class="hotBar">
            <item v-for="i in randomAnimeList()" :key="i.name" :info=i />
        </div>
        <p class="foot">Copyright ©2023</p>
    </div>
</template>
<script>
import item from './item';
import {mapState} from 'vuex'
export default {
    name: 'barContent',
    components: {
        item
    },
    computed:{
        ...mapState(['filmAnimeList','tvAnimeList']),
        info1(){
           return this.randomAnimeList()
        },
        info2(){
           return this.randomAnimeList()
        },
        info3(){
           return this.randomAnimeList()
        },
        info4(){
           return this.randomAnimeList()
        }
    },
    methods:{
        //随机化
        randomAnimeList(){
            if(this.$route.name=="filmMode"){
                return [...this.filmAnimeList].sort(()=>Math.random()-0.5).slice(0,6)
            
            }else{
                return [...this.tvAnimeList].sort(()=>Math.random()-0.5).slice(0,6)
            }
          
        }
    },
    mounted(){
        
    }
}
</script>
<style scoped>
h3:not(:first-of-type){
    margin: 2rem 0 1rem 0;
}
.foot{

    text-align: center;
    margin-top: 4rem;
}
h3:first-of-type{
    margin-top: 1rem;
    margin-bottom: 1rem;
}
#barContent {
    display: flow-root;
    min-height: 100svh;
    width: 100%;
    margin-top: 1rem;
    box-sizing: border-box;
    padding: 1rem;
}
.hotBar{
    display: flex;
    justify-content: space-between;
}
</style>