<template>
  <form id="collect">
    <headerBarVue :isShow="true"/>
    <form id="searchBox">
      <input type="search" spellcheck="true" id="textSearch" required v-model="collectSearchContent">
      <label for="textSearch"><svg width="100%" fill="none" stroke="var(--fontColor)" stroke-width="1.5"
          viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg" aria-hidden="true">
          <path stroke-linecap="round" stroke-linejoin="round"
            d="M21 21l-5.197-5.197m0 0A7.5 7.5 0 105.196 5.196a7.5 7.5 0 0010.607 10.607z"></path>
        </svg></label>
      <p></p>
    </form>
    <div id="collectBox">
      <div class="collectContainer" v-for="(value,key) in collectGet" :key="key">
        <h2>{{ key }}收藏</h2>
        <ul>
          <li v-for="(item,index) in value" :key="index">
            <ul>
              <li>{{ index+1 }}</li>
              <li @click="go(item)"><img :src="item[1]" alt=""></li>
              <li>
                <p>{{ item[0] }}</p>
                <p>{{ item[3] == "" ? "剧场版" : item[3]}}</p>
                <p>{{ item[2] }}</p>
                <aside @click="collectMove(item[0])"><svg fill="none" stroke="currentColor" stroke-width="1.5" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg" aria-hidden="true">
                  <path stroke-linecap="round" stroke-linejoin="round" d="M20.25 7.5l-.625 10.632a2.25 2.25 0 01-2.247 2.118H6.622a2.25 2.25 0 01-2.247-2.118L3.75 7.5m6 4.125l2.25 2.25m0 0l2.25 2.25M12 13.875l2.25-2.25M12 13.875l-2.25 2.25M3.375 7.5h17.25c.621 0 1.125-.504 1.125-1.125v-1.5c0-.621-.504-1.125-1.125-1.125H3.375c-.621 0-1.125.504-1.125 1.125v1.5c0 .621.504 1.125 1.125 1.125z"></path>
                </svg>删除</aside>
              </li>
            </ul>
          </li>
          <li :class="[`collectNoneState${collectListState[key]}`]">快去<i @click="collectGo(key,$event.target)">{{ key }}</i>添加你的收藏吧</li>
        </ul>
      </div>
    </div>
  </form>
</template>

<script>
import headerBarVue from '../content/headerBar.vue';
export default {
  name: 'collect',
  data(){
    return {
      // 用以搜索
      collectSearchContent: "",
      // 用以判断两个列表是否为空
      collectListState: {TV动画: 0,剧场版: 0}
    }
  },
  components: {
    headerBarVue,
  },
  computed: {
    collectGet(){
      let collectContent = {};
      if(this.$store.state.logoinUesr){
        collectContent = {TV动画: this.$store.state.logoinUesr.collectList.filter(item => item[3] !== ""),剧场版: this.$store.state.logoinUesr.collectList.filter(item => item[3] == "")};
        for(let key in collectContent){
          collectContent[key] != "" && (this.collectListState[key] = 2) || (this.collectListState[key] = 1);
        }
        // 搜索功能
        if(this.collectSearchContent != ""){
          return {TV动画: collectContent.TV动画.filter(item => item[0].includes(this.collectSearchContent)),剧场版: collectContent.剧场版.filter(item => item[0].includes(this.collectSearchContent))}
        }
      }else{
        collectContent = {TV动画: [],剧场版: []}
      }
      return collectContent;
    }
  },
  methods: {
    // 移除用户收藏 --写之前都没想到两行代码解决
    collectMove(e){
      this.$store.state.logoinUesr.collectList = this.$store.state.logoinUesr.collectList.filter(item => item[0] !== e);
      localStorage.setItem("logoinUesr", JSON.stringify(this.$store.state.logoinUesr));
    },
    // 去收藏
    collectGo(destination,e){
      if(e.parentNode.classList.contains("collectNoneState0")){
        this.$router.push({path: '/logoin'});
      }else{
        switch(destination){
          case "TV动画":
            this.$router.push({path: '/index/recommend'});
            break;
          case "剧场版":
            this.$router.push({path: '/index/filmMode'});
            break;
        }
      }
    },
    // 实时搜索
    collectSearch(e){
      e.preventDefault();
      this.collectContent = {TV动画:{},剧场版:{}}
      // console.log(this.collectContent)
    },
    go(info){
      // 使用对象
      let collectElement = {imgUrl: info[1],introduce: info[2],name: info[0],site: info[3]}
      console.log(collectElement)
      this.$store.commit('goPlay',{router:this.$router,info:collectElement})
    },
  }
}
</script>

