<template>
    <aside >
        <header></header>
        <span title="侧边栏折叠" @click="$emit('colseAside')"><ion-icon name="arrow-back-outline"></ion-icon><ion-icon name="arrow-back-outline"></ion-icon></span>
        <footer>
            <ul class="upper_half">
                <li>
                    <router-link to="/index" active-class=" host">
                        <ion-icon name="home-sharp"></ion-icon>
                        <p>首页</p>
                    </router-link>
                </li>
                <li>
                    <router-link to="/collect" active-class=" host">
                        <ion-icon name="flash-sharp"></ion-icon>
                        <p>收藏</p>
                    </router-link>
                </li>
                <li>
                    <router-link to="/logoin" active-class=" host">
                        <ion-icon name="person-circle-sharp"></ion-icon>
                        <p>用户</p>
                        <p>hi~{{ $store.state.logoinUesr.name }}<span v-if="!$store.state.logoinUesr">请登录~</span></p>
                    </router-link>
                </li>
            </ul>
            <ul class="lower_half">
                <li><label for="changeBg">
                        <ion-icon name="sunny-sharp"></ion-icon>
                        <input type="checkbox" class="changeBg" id="changeBg">
                    </label></li>
                <li @click="logoout"><ion-icon name="log-out-outline"></ion-icon></li>
            </ul>
        </footer>
    </aside>
</template>
<script>
export default {
    name: 'indexAside',
    methods: {
        logoout(){
            localStorage.removeItem('logoinUesr');
            this.$router.go(0);
            // YQ - 筛选当前用户更改其收藏列表
            let collectData = JSON.parse(localStorage.getItem("uesrList")).map(item =>{
                if(item.name == this.$store.state.logoinUesr.name){
                    return this.$store.state.logoinUesr;
                }else{
                    return item;
                }
            });
            // 对localStorage中当前用户存储收藏列表更新
            localStorage.setItem("uesrList", JSON.stringify(collectData));
        }
    }
}
</script>
<style scoped>
.host{
    color: var(--hovercolor);
}
aside {
    overflow: hidden;
    grid-row: 2 span;
    display: grid;
    grid-template-rows: auto auto 1fr;
    transition: all .25s ease-in;
}


/* 循环箭头 */
aside>header {
    height: 4rem;
    width: inherit;
    display: flex;
    justify-content: center;
    align-items: center;
}

aside>span {
    display: flex;
    overflow: hidden;
    cursor: pointer;
    margin: 5vmin 0;
}

aside>span ion-icon {
    flex: 0 0 100%;
    position: relative;
    left: 0;
    transition: left .5s;
}

aside>span:hover ion-icon {
    left: -100%;
}

/* 侧栏 */
aside footer {
    display: grid;
    grid-template-rows: 38% 18%;
    align-content: space-between;
    padding-bottom: 5vmin;
}

aside footer>* {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

aside footer li {
    /* flex: auto; */
    padding: 30% 0;
    user-select: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all .25s;
    position: relative;
}
ion-icon{cursor: pointer;}

aside footer li a {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

aside footer li a:hover {
    color: var(--hovercolor);
}

aside footer li:has(ion-icon:hover),aside footer li:has(p:hover){
    transform: scale(1.1);
    color: var(--hovercolor);
    flex-grow: 1;
}
aside li:nth-of-type(3) p:nth-of-type(2){
    display: block;
    width: 3rem;
    font-size: .5em;
    text-align: center;
}
aside footer li:hover  p:nth-of-type(2),
aside footer li:hover  p:nth-of-type(2){
    display: block;
}
.changeBg {
    display: none;
}
.logoin {
    position: absolute;
    width: 100%;
    height: 100svh;
    background-color: rgba(255, 255, 255, .5);
    top: 0%;
    left: 0%;
    z-index: 2;
}
</style>