<template>
    <div id="carousel">
        <main class="content">
            <ul v-for="item in arr" :key="item.name" @click="go(item)">
                <li><img :src="item.imgUrl" :alt="item.url"></li>
                <li>
                    <p>
                        <span>
                            《{{ item.name }}》
                        </span>
                        {{ item.introduce }}
                    </p>
                </li>
            </ul>
        </main>
        <ul class="avatar">
            <li v-for="item in arr" :key="item.name" @click="leftmove($event)">

            </li>
        </ul>
    </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
    name: 'carousel',
    data() {
        return {

            bf_value: 0,
            now_value: 0,
            looptime: 1
        }
    },
    computed: {
        ...mapState(['filmAnimeList', 'tvAnimeList']),

        arr() {
            if (this.$route.name == "filmMode") {
                return [...this.filmAnimeList].sort(() => Math.random() - 0.5).slice(0, 3)

            } else {
                return [...this.tvAnimeList].sort(() => Math.random() - 0.5).slice(0, 3)
            }
        },
        oli() {
            return document.querySelectorAll(".avatar li");
        },
        contents() {
            return document.querySelectorAll(".content ul");
        }
    },
    methods: {
        go(info) {
            this.$store.commit('goPlay', { router: this.$router, info })
        },



        // 按钮点击
        leftmove({ target }) {
            this.oli.forEach((item, index) => {
                item.style = "";
                target == item && (this.bf_value = this.now_value) & (this.now_value = index);
            });
            this.carousel_img();
            this.looptime = 3;
        },
        // 按钮轮播
        carousel_btn() {
            this.oli[this.bf_value < 0 ? this.arr.length - 1 : this.bf_value].animate(
                [
                    { left: "0rem" },
                    { left: (this.now_value - (this.bf_value < 0 ? 2 : this.bf_value)) * 1.5 + "rem", offset: 0.3 },
                    { left: "0rem" }
                ],
                {
                    duration: 250,
                    iterations: 1
                }
            )
            setTimeout(() => {
                this.oli[this.now_value].style = "border-radius: 5%;"
            }, 125)
        },
        // 图片轮播
        carousel_img() {
            this.oli.forEach((item, index) => {
                item.style = "";
            })
            this.contents[this.now_value + 1 < this.contents.length ? this.now_value + 1 : 0].style = "transform: scale(.75) rotateY(15deg) translate3d(-50%,0,-5rem);"
            this.contents[this.now_value].style = "transform: scale(1) rotateY(0deg) translate3d(0,0,5rem);"
            this.contents[this.now_value - 1 < 0 ? this.arr.length - 1 : this.now_value - 1].style = "transform: scale(.75) rotateY(-15deg) translate3d(50%,0,-5rem);"
            this.carousel_btn();
        },
        // 整体轮播循环
        carousel_loop() {
            setInterval(() => {
                this.looptime--;
                if (this.looptime == 0) {
                    this.bf_value = this.now_value;
                    this.now_value < this.arr.length - 1 ? this.now_value++ : (this.now_value = 0);
                    this.carousel_img();
                    this.looptime = 1;
                }
            }, 2000);
        }
    },

    mounted() {
        // 轮播按钮初始化
        this.oli[this.bf_value].style = "border-radius: 5%";
        this.contents[0].style = "z-index: 1";
        // 整体轮播循环
        this.carousel_loop()
    }
}
</script>
<style scoped>
#carousel {
    margin: auto;
    width: 100%;
    align-items: center;
    display: grid;
    grid-template-rows: auto auto;
    position: relative;
    background-color: var(--bgWihte);
    border-radius: 1rem;
}

.content {
    position: relative;
    display: flex;
    justify-content: center;
    width: 100%;
    aspect-ratio: 6;
    transform-style: preserve-3d;
    perspective: 60rem;
    z-index: 1;
}

.content ul {
    width: 45%;
    height: 100%;
    position: absolute;
    transition: transform .5s;
    box-shadow: 0 .5rem .75rem rgba(0, 0, 0, .5);
    border-radius: .8rem;
    overflow: hidden;
    cursor: pointer;
}

.content ul li:first-child {
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    padding: .35rem;
    box-sizing: border-box;
}

.content ul li:first-child img {
    width: 100%;
    height: 100%;
    /* box-sizing: border-box; */
    border-radius: .5rem;
}

.content ul li:last-child {
    padding: .5rem;
    background: linear-gradient(rgba(200, 200, 200, .1), rgba(64, 64, 64, .8));
    /* backdrop-filter: blur(1rem); */
    border-radius: 0 0 .5rem .5rem;
    width: calc(100% - 1.5rem);
}

.content ul li:last-child span {
    font-weight: bold;
}

.content ul li:last-child p {
    width: 20rem;
    margin-left: calc(100% - 20rem);
    color: var(--bgWihte);
    text-indent: 1rem;
    /* mix-blend-mode: difference; */
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}

.content ul li:last-child {
    color: #FFF;
    position: absolute;
    left: .25rem;
    bottom: .25rem;
}

img {

    height: 100%;
    object-fit: cover;
}

/* 轮播图按钮 */
.avatar {
    display: flex;
    justify-content: center;
    padding: 1rem;
    filter: contrast(5);
    background-color: var(--bgWihte);
    position: relative;
    z-index: 0;
    border-radius: 0 0 .8rem .8rem;
}

.avatar li {
    border-radius: 50%;
    width: 1rem;
    margin: 0 .25rem;
    aspect-ratio: 1;
    background-color: var(--fontColor);
    position: relative;
    cursor: pointer;
    filter: blur(.15rem);
}</style>
